import util from '../util/util.js'
import addMessages from '@/components/addMessages/addMessages.vue';
export default {
	data() {
		return {
			pageData: {
				initData: []
			},
			userName: '',
			showToast: false
		}
	},
	components:{
		addMessages
	},
	onLoad() {
		this.init()
	},
	onShow() {
		
	},
	onShareAppMessage(){
		return {
			title: '校招offer知多少,就上校招offer',
			path: '/pages/index/index'
		}
	},
	created() {
		
	},
	computed:{
		rate: function() {
			return function(arr){
				let sum = 0
				sum = arr.reduce((total,current,index,arr) => {
					return total + current
				})
				return (sum/arr.length)*5
			}
		}
	},
	methods: {
		init(){
			this.getData()
			try{
				const data = uni.getStorageSync('userName');
				if(data && data.userName){
					this.userName = data.userName
				} else {
					const name = this.randomName()
					uni.setStorage({
					    key: 'userName',
					    data: {"userName":name},
					    success: function () {
					        console.log('userName success');
					    }
					});
					this.userName = name
				}
			}catch(e){
				console.log(e)
			}
		},
		randomName(){
		 return	util.randomName([true,false][Math.floor(Math.random()*2)])
		},
		getData: async function() {
			const { data } = await util.request({},'getData')
			this.pageData = {
				...this.pageData,
				initData : data
			}
			this.showToast = false
		},
		search: async function(e) {
			let arr = []
		 	arr = e.value.split('+')
			if(e.value){
				if(arr.length === 1){
					const { data } = await util.request({post : arr[0].trim()},'searchData')
					if (data.length === 0) {
						this.showToast = true
						uni.showToast({
							title: '不好意思~暂未收录，赶紧去爆料吧',
							icon: 'none',
							duration: 2000
						})
					} else {
						this.showToast = false
					}
					this.pageData = {
						...this.pageData,
						initData : data
					}
				} else if (arr.length === 2) {
					const { data } = await util.request({post : arr[0].trim(),otherValue : arr[1].trim()},'searchData')
					if (data.length === 0) {
						this.showToast = true
						uni.showToast({
							title: '不好意思~暂未收录，赶紧去爆料吧',
							icon: 'none',
							duration: 2000
						})
					} else {
						this.showToast = false
					}
					this.pageData = {
						...this.pageData,
						initData : data
					}
				} else {
					uni.showToast({
						title: '请输入正确组合查询格式',
						icon: 'none'
					})
				}
			} else {
				this.getData()
				uni.showToast({
					title: '没有查询内容，已为您展示默认数据',
					icon: 'none'
				})
			}	
		},
		getDetail(_id) {
			uni.navigateTo({
				url: '/pages/detail/detail?_id='+_id+'&&userName='+this.userName
			})
		}
	}
}
